<td style="text-align:left">String</td>
<td style="text-align:left">--</td>
</tr>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">类型</td>
<td style="text-align:left">String</td>
<td style="text-align:left">normal</td>
</tr>
<tr>
<td style="text-align:left">inputClass</td>
<td style="text-align:left">input元素自定义class</td>
<td style="text-align:left">String</td>
<td style="text-align:left">--</td>
</tr>
<tr>
<td style="text-align:left">popupClass</td>
<td style="text-align:left">popup自定义class</td>
<td style="text-align:left">String</td>
<td style="text-align:left">--</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">禁用</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">clearable</td>
<td style="text-align:left">清除</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">rangeSeparator</td>
<td style="text-align:left">范围分隔符</td>
<td style="text-align:left">String</td>
<td style="text-align:left">&quot;~&quot;</td>
</tr>
<tr>
<td style="text-align:left">format</td>
<td style="text-align:left">格式化</td>
<td style="text-align:left">String</td>
<td style="text-align:left">&quot;YYYY-MM-DD&quot;</td>
</tr>
<tr>
<td style="text-align:left">local</td>
<td style="text-align:left">语言切换，默认中文</td>
<td style="text-align:left">Object</td>
<td style="text-align:left">{<br>dow: 1, // Monday is the first day of the week<br>hourTip: '选择小时', // tip of select hour<br>minuteTip: '选择分钟', // tip of select minute<br>secondTip: '选择秒数', // tip of select second<br>yearSuffix: '年', // format of head<br>monthsHead: '1月_2月_3月_4月_5月_6月_7月_8月_9月_10月_11月_12月'.split('<em>'), // months of head<br>months: '一月_二月_三月_四月_五月_六月_七月_八月_九月_十月_十一月_十二月'.split('</em>'), // months of panel<br>weeks: '一_二_三_四_五_六_日'.split('_'), // weeks<br> cancelTip: '取消', // default text for cancel button <br>, submitTip: '提交' // default text for submit button <br>}</td>
</tr>
<tr>
<td style="text-align:left">disabledDate</td>
<td style="text-align:left">设置禁止选中的日期</td>
<td style="text-align:left">Function</td>
<td style="text-align:left">(time, format)=&gt;{return false}</td>
</tr>
<tr>
<td style="text-align:left">showButtons</td>
<td style="text-align:left">显示按钮</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">placeholder</td>
<td style="text-align:left">placeholder</td>
<td style="text-align:left">String</td>
<td style="text-align:left">--</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">Event Name</th>
<th style="text-align:left">Description</th>
<th style="text-align:left">Parameters</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">confirm</td>
<td style="text-align:left">确定按钮触发事件</td>
<td style="text-align:left">the value component's binding value</td>
</tr>
<tr>
<td style="text-align:left">cancel</td>
<td style="text-align:left">取消按钮触发事件</td>
<td style="text-align:left">--</td>
</tr>
<tr>
<td style="text-align:left">clear</td>
<td style="text-align:left">清楚按钮触发事件</td>
<td style="text-align:left"></td>
</tr>
</tbody>
</table>

</section></template>

<script>
export default {
    data(){
        return {
            time: new Date(),
            range: [new Date(),new Date()],
            emptyTime: '',
            emptyRange: [],
            local: {
              dow: 0, // Sunday is the first day of the week
              hourTip: 'Select Hour', // tip of select hour
              minuteTip: 'Select Minute', // tip of select minute
              secondTip: 'Select Second', // tip of select second
              yearSuffix: '', // suffix of head year
              monthsHead:'January_February_March_April_May_June_July_August_September_October_November_December'.split('_'), // months of head
              months: 'Jan_Feb_Mar_Apr_May_Jun_Jul_Aug_Sep_Oct_Nov_Dec'.split('_'), // months of panel
              weeks: 'Su_Mo_Tu_We_Th_Fr_Sa'.split('_'), // weeks,
              cancelTip: 'cancel',
              submitTip: 'confirm'
            }
        }
    },
      methods: {
            disabledDate (time) {
              var day = time.getDay();
              return day === 0 || day === 6;
            },
            confirm(){
              alert('按钮')
          }
          }
}
</script>


