    <lu-checkbox value="label3" label="默认选中" v-model="obj.label3" name="a"></lu-checkbox>
    <p>obj:{{obj}}</p>
</div>


</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
