</div></demo-block>
<h3>数组用法</h3>
<div class="demo-block">
    <div v-for="item,index in list" class="row">
        <lu-Checkbox v-model="arr" :value="item" :label="item.label" :key="index"></lu-Checkbox>
    </div>
    <p>arr:{{arr}}</p>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
