</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>尺寸</td>
<td>boolean/array</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>value</td>
<td>值</td>
<td>string/object</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>文字</td>
<td>string</td>
<td>—</td>
<td>如果不穿值，默认等于value</td>
</tr>
</tbody>
</table>
<h3>Event</h3>
<p>拥有原生的方法，绑定方式一样</p>


</section></template>
<style>
.row{
    padding:5px 0;
}
</style>
<script>
    export default {
        data(){
            return {
                arr:[],
                obj:{
                    label1:false,
                    label2:true,
                    label3:true,
                },
                list:[
                    {
                        value:'1',
                        label:'西瓜',
                    },
                    {
                        value:'2',
                        label:'苹果',
                    },
                    {
                        value:'3',
                        label:'香蕉',
                    },
                    {
                        value:'4',
                        label:'梨子',
                    }
                ]
            }
        },
        mounted(){
            this.arr.push(this.list[0])
        }
    }
</script>


