<template><section><h1>Modal 对话框</h1>
<hr>
<h3>基础用法</h3>
<p>全局引入lulu后，直接调用这些方法<code v-pre="" class="code_inline">this.$modal</code>,<code v-pre="" class="code_inline">this.$alert</code>,<code v-pre="" class="code_inline">this.$prompt</code>,<code v-pre="" class="code_inline">this.$confirm</code>.</p>
<p>传入的参数是一个对象</p>
<pre v-pre=""><code v-pre="" class="language-js">    {
        <span class="hljs-attr">title</span>: <span class="hljs-string">'提示'</span>,
        <span class="hljs-attr">cancleBtn</span>: <span class="hljs-string">'取消'</span>,
        <span class="hljs-attr">confirmBtn</span>: <span class="hljs-string">'确定'</span>,
        <span class="hljs-attr">message</span>: <span class="hljs-string">''</span>,
        <span class="hljs-attr">level</span>:<span class="hljs-string">''</span>，<span class="hljs-comment">//重要级别info,success,warning,question,danger</span>
        type: <span class="hljs-string">''</span>,<span class="hljs-comment">//类型为alert,prompt,confirm，调用modal才需要输入type</span>
    }
</code></pre>
<div class="demo-block">
    <lu-button @click="modal">自定义弹窗</lu-button>
    <lu-button type="success" @click="alert">alert</lu-button>
    <lu-button type="primary" @click="prompt">prompt</lu-button>
    <lu-button type="danger" @click="confirm">confirm</lu-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
    <lu-button @click="modal">自定义弹窗</lu-button>
    <lu-button type="success" @click="alert">alert</lu-button>
    <lu-button type="primary" @click="prompt">prompt</lu-button>
    <lu-button type="danger" @click="confirm">confirm</lu-button>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"modal"</span>&gt;</span>自定义弹窗<span class="hljs-tag">&lt;/<span class="hljs-name">lu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"alert"</span>&gt;</span>alert<span class="hljs-tag">&lt;/<span class="hljs-name">lu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"prompt"</span>&gt;</span>prompt<span class="hljs-tag">&lt;/<span class="hljs-name">lu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"confirm"</span>&gt;</span>confirm<span class="hljs-tag">&lt;/<span class="hljs-name">lu-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">show</span>:<span class="hljs-literal">false</span>
            }
        },
        <span class="hljs-attr">methods</span>:{
            modal(){
                <span class="hljs-keyword">const</span> h = <span class="hljs-keyword">this</span>.$createElement;
                <span class="hljs-keyword">this</span>.$modal({
                    <span class="hljs-attr">title</span>:<span class="hljs-string">'自定义标题'</span>,
                    <span class="hljs-attr">cancleBtn</span>:<span class="hljs-string">'取消按钮'</span>,
                    <span class="hljs-attr">confirmBtn</span>:<span class="hljs-string">'确定按钮'</span>,
                    <span class="hljs-attr">message</span>:h(<span class="hljs-string">'p'</span>, <span class="hljs-literal">null</span>, [
                        h(<span class="hljs-string">'span'</span>, <span class="hljs-literal">null</span>, <span class="hljs-string">'内容可以是 '</span>),
                        h(<span class="hljs-string">'i'</span>, { <span class="hljs-attr">style</span>: <span class="hljs-string">'color: teal'</span> }, <span class="hljs-string">'VNode'</span>)
                    ])
                }).then(<span class="hljs-function"><span class="hljs-params">action</span>=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action)
                }).catch(<span class="hljs-function"><span class="hljs-params">action</span>=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action)
                })
            },
            alert(){
                <span class="hljs-keyword">this</span>.$alert({
                    <span class="hljs-attr">message</span>:<span class="hljs-string">'这是一个alert窗口'</span>
                }).then(<span class="hljs-function"><span class="hljs-params">action</span>=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action)
                }).catch(<span class="hljs-function"><span class="hljs-params">action</span>=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action)
                })
            },
            prompt(){
                <span class="hljs-keyword">this</span>.$prompt({
                    <span class="hljs-attr">message</span>:<span class="hljs-string">'prompt请输入'</span>
                }).then(<span class="hljs-function"><span class="hljs-params">action</span>=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action)
                }).catch(<span class="hljs-function"><span class="hljs-params">action</span>=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action)
                })
            },
            confirm(){
                <span class="hljs-keyword">this</span>.$confirm({
                    <span class="hljs-attr">message</span>:<span class="hljs-string">'这是一个confirm窗口'</span>
                }).then(<span class="hljs-function"><span class="hljs-params">action</span>=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action)
                }).catch(<span class="hljs-function"><span class="hljs-params">action</span>=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action)
                })
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>


</code></pre>
</div></demo-block>
<h3>弹窗的级别</h3>
<p>让用户知道这个弹窗重要程度的标志</p>
<div class="demo-block">
    <lu-button @click="info" type="primary" plain="">info</lu-button>
    <lu-button type="success" @click="success">success</lu-button>
    <lu-button type="warning" @click="warning">warning</lu-button>
    <lu-button type="primary" @click="question">question</lu-button>
    <lu-button type="danger" @click="danger">danger</lu-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><lu-button type="success" @click="success">success</lu-button>
    

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"success"</span> &gt;</span>success<span class="hljs-tag">&lt;/<span class="hljs-name">lu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="actionscript">
        success(){
                <span class="hljs-keyword">this</span>.$modal({
                    level:<span class="hljs-string">'success'</span>,
                    message:<span class="hljs-string">'success类型的窗口'</span>
            })
        },
    </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>

</section></template>

<script>
    export default{
        data(){
            return {
                show:false
            }
        },
        methods:{
             modal(){
                const h = this.$createElement;
                this.$modal({
                    title:'自定义标题',
                    cancleBtn:'取消按钮',
                    confirmBtn:'确定按钮',
                    message:h('p', null, [
                        h('span', null, '内容可以是 '),
                        h('i', { style: 'color: teal' }, 'VNode')
                    ])
                }).then(action=>{
                    console.log(action)
                }).catch(action=>{
                    console.log(action)
                })
            },
            alert(){
                this.$alert({
                    message:'这是一个alert窗口'
                }).then(action=>{
                    console.log(action)
                }).catch(action=>{
                    console.log(action)
                })
            },
            prompt(){
                this.$prompt({
                    message:'prompt请输入'
                }).then(action=>{
                    console.log(action)
                }).catch(action=>{
                    console.log(action)
                })
            },
            confirm(){
                this.$confirm({
                    message:'这是一个confirm窗口'
                }).then(action=>{
                    console.log(action)
                }).catch(action=>{
                    console.log(action)
                })
            },
            info(){
                this.$modal({
                    level:'info',
                    message:'info类型的窗口'
                })
            },
            success(){
                this.$modal({
                    level:'success',
                    message:'success类型的窗口'
                })
            },
            warning(){
                this.$modal({
                    level:'warning',
                    message:'warning类型的窗口'
                })
            },
            question(){
                this.$modal({
                    level:'question',
                    message:'question类型的窗口'
                })
            },
            danger(){
                this.$modal({
                    level:'danger',
                    message:'danger类型的窗口'
                })
            }
        }
    }
</script>


