<template><section><h1>Input 输入框</h1>
<hr>
<h3>基础用法</h3>
<p>我们为 <code v-pre="" class="code_inline">&lt;lu-input /&gt;</code>输入框定义了三种尺寸（默认、中、小），高度分别为 40px、34px 和 28px。</p>
<div class="demo-block">
    <lu-input size="large" placeholder="large" style="width:180px"></lu-input>
    <lu-input size="default" readonly placeholder="readonly" style="width:180px"></lu-input>
    <lu-input size="small" disabled placeholder="small disabled" style="width:180px"></lu-input>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><lu-input size="large" placeholder="large" style="width:180px"></lu-input>
    <lu-input size="default" readonly placeholder="readonly" style="width:180px"></lu-input>
    <lu-input size="small" disabled placeholder="small disabled" style="width:180px"></lu-input>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-input</span>  <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"large"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:180px"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">lu-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-input</span>  <span class="hljs-attr">size</span>=<span class="hljs-string">"default"</span> <span class="hljs-attr">readonly</span>  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"readonly"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:180px"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">lu-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-input</span>  <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"small disabled"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:180px"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">lu-input</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>icon的用法</h3>
<div class="demo-block">
    <lu-input size="large" type="search" v-model="val" placeholder="placeholder" suffix="iconfont icon-search" @icon-click="click" style="width:180px"></lu-input>
    <lu-input type="search" clearable="" v-model="val" placeholder="placeholder" prefix="iconfont icon-peoplefill" style="width:180px"></lu-input>
    <lu-input size="small" type="search" clearable="" v-model="val" placeholder="placeholder" style="width:180px" suffix="iconfont icon-repeal" prefix="iconfont icon-profile"></lu-input>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><lu-input size="large" type="search" v-model="val" placeholder="placeholder" suffix="iconfont icon-search" @icon-click="click" style="width:180px"></lu-input>
    <lu-input type="search" clearable="" v-model="val" placeholder="placeholder" prefix="iconfont icon-peoplefill" style="width:180px"></lu-input>
    <lu-input size="small" type="search" clearable="" v-model="val" placeholder="placeholder" style="width:180px" suffix="iconfont icon-repeal" prefix="iconfont icon-profile"></lu-input>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-input</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"search"</span>  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"placeholder"</span>  <span class="hljs-attr">suffix</span>=<span class="hljs-string">"iconfont icon-search"</span> <span class="hljs-attr">@icon-click</span>=<span class="hljs-string">"click"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:180px"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">lu-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-input</span>  <span class="hljs-attr">type</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">clearable</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"placeholder"</span>  <span class="hljs-attr">prefix</span>=<span class="hljs-string">"iconfont icon-peoplefill"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:180px"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">lu-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">lu-input</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">clearable</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"placeholder"</span>  <span class="hljs-attr">style</span>=<span class="hljs-string">"width:180px"</span> <span class="hljs-attr">suffix</span>=<span class="hljs-string">"iconfont icon-repeal"</span><span class="hljs-attr">prefix</span>=<span class="hljs-string">"iconfont icon-profile"</span> &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">lu-input</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>textarea类型</h3>
<div class="demo-block">
    <lu-input type="textarea" rows="4" cols="60"></lu-input>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><lu-input type="textarea" rows="4"></lu-input>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">lu-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">rows</span>=<span class="hljs-string">'4'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">lu-input</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<p>拥有原生input的属性和方法</p>
<table class="table"><thead>
<tr>
<th>参数</th>
<th style="text-align:center">说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>clearable</td>
<td style="text-align:center">按钮</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>prefix</td>
<td style="text-align:center">前置icon的class名</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>suffix</td>
<td style="text-align:center">后置icon的class名</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td style="text-align:center">大小只对input有效</td>
<td>string</td>
<td>large，small,default</td>
<td>default</td>
</tr>
</tbody>
</table>
<h3>Event</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th style="text-align:center">说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>clear</td>
<td style="text-align:center">清除按钮回调</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>icon-click</td>
<td style="text-align:center">后置按钮点击回调</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>

</section></template>

<script>
export default {
    data(){
        return {
            val:''
        }
    },
    methods:{
        click(e){
            alert('点击了suffix-icon')
        }
    }
}
</script>


